import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    provider:null,
    wallet:null,//钱包
    contract:null,//合约
    userAddress:null,
    signedContract:null,
    site:[],
    mysite:[]
  },
  mutations: {
    initprovider(state,provider){
      state.provider=provider
      console.log(state.provider)
    },
    initcontract(state,contract){
      state.contract=contract
    },
    initsignedContract(state,signedContract){
      state.signedContract=signedContract
    },
    inituserAddress(state,userAddress){
      state.userAddress=userAddress
      console.log(state.userAddress)
    },
    addSite(state,Site){
      state.site.push(Site)
    },
    addMySite(state,Site){
      state.mysite.push(Site)
    }
  },
  actions: {
    iniP({commit},prov){
      console.log("hello")
      commit("initprovider",prov.provider)
    },
    iniC({commit},prov){
      console.log("hello")
      commit("initcontract",prov.contract)
    },
    iniS({commit},prov){
      console.log("hello")
      commit("initsignedContract",prov.signedContract)
    },
    iniU({commit},prov){
      console.log("hello")
      commit("inituserAddress",prov.userAddress)
    },
    addSite({commit},prov){
      commit("addSite",prov.Site)
    },
    addMySite({commit},prov){
      commit("addMySite",prov.Site)
    }
  },
  modules: {

  }
})
